import {
	getSongUrl
} from "@/api/song.js"
import {
	mapMutations
} from 'vuex'
export default {
	props: {
		title: {
			type: String,
			required: true
		},
		songList: {
			type: Array,
			required: true
		}
	},
	data() {
		return {

		};
	},
	methods: {
		...mapMutations(['setPlayingSong']),
		async playSong(song) {
			let routes = getCurrentPages(); // 获取当前打开过的页面路由数组
			let curRoute = routes[routes.length - 1].route;
			await this.getUrl(song);
			if (curRoute != "pages/songDetail/song-detail") {
				uni.navigateTo({
					url: '/pages/songDetail/song-detail',
					success: r => {},
					fail: () => {},
					complete: () => {}
				});
			}
		},
		async getUrl(song) {
			this.setPlayingSong(song);
			getSongUrl(song.id).then(res => {
				let songList = res.data.map(item => {
					return {
						url: item.url,
						id: item.id
					};
				});
				let url = songList[0].url;
				if (!url) {
					uni.showToast({
						title: '暂无版权',
						icon: 'none',
						mask: true,
					});
					return;
				}
				this.$bgAudioMannager.title = song.name;
				this.$bgAudioMannager.singer = song.artist;
				this.$bgAudioMannager.coverImgUrl = song.cover;
				this.$bgAudioMannager.src = url;
			}).catch(e => {
				console.log(e);
			});
		}
	}
}
